.emailRow{
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
    z-index: 999;
    height: 17%;
}
.emailRow:hover{
    border-top: 1px solid whitesmoke;
    box-shadow: 0px 4px 4px -2px rgba(0,0, 0, 0.24);
}
.emailRow__options{
display: flex;
}
.emailRow__message{
    display: flex;
    flex: 0.8;
    align-items: center;
    font-size: 12px;
}
.emailRow__title{
    font-size: 13px;
    flex: 0.3;
}
.emailRow__time{
    padding-right: 15px;
    font-size: 9px;
    font-weight: bold;
}